@import url(https://fonts.googleapis.com/css2?family=Carter+One&family=Epilogue:wght@100;400&family=Merriweather+Sans:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.recipe-details-container {
  background-color: white;
  padding: 10px;
  padding-bottom: 120px;
}

/* ingredientes */
.ingredients {
  margin: auto;
  max-width: 1000px;
  width: 100%;
}

.ingredients ul {
  background-color: gainsboro;
  border-radius: 5px;
  list-style-type: none;
  margin: auto;
  padding: 10px;
}

/* instruções da receita */
.preparation-method {
  margin: auto;
  max-width: 1000px;
  width: 100%;
}

.in-progress-instructions {
  margin: auto;
  max-width: 1000px;
  padding-bottom: 120px;
  width: 100%;
}

.in-progress-instructions p, .preparation-method p {
  background-color: gainsboro;
  border-radius: 5px;
  margin: auto;
  padding: 10px;
}

/* video */
.video-container {
  display: flex;
  flex-direction: column;
  margin: auto;
  max-width: 1000px;
  width: 100%;
}

.video-container iframe {
  align-self: center;
  border-radius: 5px;
  width: 100%;
}

/* cards de recomendação */
.recommendations {
  margin: auto;
  max-width: 1000px;
  width: 100%;
}

.cards-container {
  overflow-x: scroll;
  padding-bottom: 10px;
  white-space: nowrap;
}

.card-photo {
  border-radius: 5px;
  height: 110px;
  object-fit: cover;
  width: 100%;
}

.recommendation-card {
  border-radius: 5px;
  box-shadow: 2px 4px 6px gainsboro;
  display: inline-block;
  margin-right: 10px;
  width: 160px;
}

.recommendation-card p, h3 {
  padding-left: 6px;
}

/* botão de iniciar receita */
.bottom-btn {
  background-color: #0261ba;
  border: none;
  bottom: 0;
  color: white;
  font-size: large;
  height: 80px;
  left: 50%;
  margin: 10px 0;
  margin-left: -170px;
  margin-top: -100px;
  padding: 20px;
  position: fixed;
  width: 340px;
}

@media ( max-width : 376px ) {

  .recommendation-card {
    width: 170px;
  }
}

@media ( max-width : 320px ) {

  .recommendation-card {
    width: 100%;
  }
}

/* cartões de receitas */
.favorite-recipe-card {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-shadow: 0 2px 4px rgb(161, 161, 161);
  display: flex;
  height: 100%;
}

.favorite-recipe-card img {
  border-top-left-radius: 10px;
  height: 100%;
  object-fit: cover;
  width: 160px;
}

.favorite-recipe-info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 10px;
  grid-row-gap: 10px;
  row-gap: 10px;
}

.media-btn-container {
  display: flex;
  justify-content: space-around;
}

.favorite-recipe-card a {
  color: black;
  font-size: medium;
  padding: 0;
}


.recipe-header-container {
  background-color: white;
  display: flex;
  flex-direction: column;
  grid-row-gap: 20px;
  row-gap: 20px;
}

.header-img {
  height: 200px;
  margin-bottom: -10px;
  object-fit: cover;
  width: 100%;
}

.recipe-header-container section {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin: auto;
  max-width: 1000px;
  width: 94%;
}

.info-container {
  display: flex;
  flex-direction: column;
}

.info-container > * {
  margin: 0;
}

.info-container p {
  color: rgb(165, 164, 164);
  font-size: 28px;
}

.btn-container {
  padding: 28px 6px;
  text-align: center;
}

.btn-container input {
  margin-left: 6px;
}

.btn-container div {
  display: inline-block;
}

footer {
  background-color: rgb(195, 20, 50);
  bottom: 0;
  position: fixed;
  width: 100%;
}

.footer-container {
  align-items: center;
  display: flex;
  justify-content: space-around;
  margin: auto;
  max-width: 1400px;
}


.search-form {
  align-items: center;
  background-color: rgb(231, 229, 229);
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;
  padding: 12px;
}

.search-form > * {
  width: 100%;
}

.search-form label > * {
  margin-right: 10px;
}

.search-input {
  border: none;
  border-radius: 4px;
  height: 20px;
  padding: 5px 0;
  text-indent: 10px;
}

.exec-search-btn {
  background-color: #0261ba;
  border: none;
  border-radius: 4px;
  color: white;
  font-size: medium;
  padding: 3px;
}

@media ( max-width : 426px ) {

  .search-form {
    flex-flow: row wrap;
    flex-direction: column;
  }
}

/* header usado na maioria das páginas */
.alternative-header {
  background-color: rgb(195, 20, 50);
  padding: 10px 0;
  width: 100%;
}

.alternative-header div {
  color: white;
  display: flex;
  height: 30px;
  padding: 0 10px;
}

.alternative-header a {
  align-self: flex-start;
  position: absolute;
}

.alternative-header h1 {
  font-size: x-large;
  margin: auto;
}

/* header da tela principal e da tela de explorar por origem */
.header-container {
  background-color: rgb(195, 20, 50);
  padding: 10px 0;
  width: 100%;
}

.header-container h1 {
  font-size: x-large;
}

.header-container div {
  color: white;
  display: flex;
  height: 30px;
  justify-content: space-around;
  margin: auto;
  max-width: 1340px;
  width: 100%;
}

@media ( max-width : 320px ) {

  .alternative-header h1 {
    font-size: large;
  }
}

.explore-page-container {
  background-color: white;
  padding-bottom: 580px;
}

.explore-options {
  align-items: center;
  display: flex;
  flex-direction: column;
  left: 50vw;
  margin: -80px 0 -10px -125px;
  position: fixed;
  top: 50vh;
}

.explore-options > button {
  background-color: white;
  border: none;
  border-radius: 6px;
  box-shadow: 0 2px 2px rgb(184, 184, 184);
  font-size: 18px;
  font-weight: 100;
}

.ingredients-container {
  align-items: center;
  background-color: white;
  display: flex;
  flex-flow: row wrap;
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;
  margin: auto;
  max-width: 1000px;
  padding-bottom: 80px;
  padding-top: 30px;
  width: 100%;
}

.ingredient-card {
  background-color: white;
  box-shadow: 0 2px 4px rgb(172, 172, 172);
  padding: 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.ingredient-card img {
  height: 130px;
}

.ingredient-card button {
  background-color: transparent;
  border: none;
}

.ingredient-card p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 120px;
}

body {
  background-attachment: fixed;
  background-color: rgb(195, 20, 50);
}

.login-container {
  margin: 170px 0;
}

.login-container h1 {
  color: white;
  font-family: Carter One , cursive;
  font-size: 45px;
  text-align: center;
}

.form-container {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.form-container > input {
  border-radius: 8px;
  font-size: medium;
  height: 40px;
  margin: 0;
  padding: 10px;
  text-indent: 10px;
  width: 270px;
}

.form-container > input:first-child {
  border: 1px solid gray;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.form-container > input:nth-child( 2 ) {
  border: 1px solid gray;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.btn-form {
  background-color: #0261ba;
  border: none;
  border-radius: 12px;
  color: white;
  font-size: large;
  height: 70px;
  margin: 10px 0;
  width: 290px;
}


.container-main {
  background-color: white;
  height: 90vh;
}

.box {
  align-items: center;
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.button {
  background-color: #c4c4c4;
  border: none;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 18px;
  font-weight: 800;
  height: 50px;
  margin: 5px;
  width: 250px;
}

.profile-email {
  font-size: 20px;
  font-weight: 100;
}


.category-buttons {
  background-color: white;
  display: flex;
  justify-content: center;
  margin: auto;
  max-width: 1000px;
  padding: 10px 0;
  width: 100%;
}

.category-buttons > button {
  background-color: rgb(231, 231, 231);
  border: none;
  color: rgb(32, 32, 32);
  font-family: sans-serif;
  font-size: small;
  margin: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
}

@media ( max-width : 470px ) {

  .category-buttons {
    flex-flow: row wrap;
  }

  .category-buttons > button {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 25vw;
  }
}

.recipe-card {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-shadow: 0 2px 4px rgb(161, 161, 161);
  width: 160px;
}

.recipe-card a {
  color: black;
  text-align: center;
  text-decoration: none;
}

.recipe-name {
  overflow: hidden;
  padding: 20px 0 10px 10px;
  text-align: start;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.recipe-img {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  height: 140px;
  object-fit: cover;
  width: 100%;
}

@media ( max-width : 320px ) {

  .recipe-card {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    box-shadow: 0 2px 4px rgb(161, 161, 161);
    width: 90%;
  }
}

.recipe-list {
  background-color: white;
  display: flex;
  flex-wrap: wrap;
  grid-gap: 16px;
  gap: 16px;
  height: 100%;
  justify-content: center;
  margin: auto;
  max-width: 1000px;
  padding-bottom: 80px;
  grid-row-gap: 16px;
  row-gap: 16px;
  width: 100%;
}

/* botões de filtragem */
.btn-filter-container {
  display: flex;
  margin: auto;
  max-width: 1000px;
  padding: 10px 0;
  width: 100%;
}

.btn-filter-container > button {
  align-items: center;
  background-color: rgb(231, 231, 231);
  border: none;
  color: rgb(32, 32, 32);
  display: flex;
  font-family: sans-serif;
  font-size: small;
  justify-content: center;
  margin: 10px;
  padding: 12px;
  width: 100%;
}

/* conteúdo principal */
.content-container {
  background-color: white;
}

/* cartões de receitas */
.made-recipe-card {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  box-shadow: 0 2px 4px rgb(161, 161, 161);
  display: flex;
  height: 100%;
}

.made-recipe-card img {
  border-top-left-radius: 10px;
  height: 100%;
  object-fit: cover;
  width: 160px;
}

.made-recipe-info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 10px;
  grid-row-gap: 10px;
  row-gap: 10px;
}

.made-recipe-info > * {
  margin: 0;
}

.made-recipe-card a {
  padding: 0;
}

.made-recipe-card input {
  align-self: flex-start;
  width: 30px;
}

.category-text, .done-date {
  font-size: small;
}

.recipe-name {
  color: black;
  font-size: medium;
  font-weight: 800;
  overflow: hidden;
  text-decoration: none;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 130px;
}

.recipe-tags {
  background-color: rgb(247, 229, 234);
  border-radius: 50px;
  padding: 2px 6px;
  text-align: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.share-icon {
  padding: 10px 10px 0 0;
}


@media ( max-width : 320px ) {

  .made-recipe-card {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    box-shadow: 0 2px 4px rgb(161, 161, 161);
    width: 90%;
  }

  .recipe-name {
    width: 100px;
  }

  .share-icon {
    overflow: hidden;
  }
}

.loading-message {
  color: white;
  font-family: sans-serif;
  font-size: large;
  font-weight: 600;
  left: 50%;
  margin: -30px 0 0 -70px;
  position: absolute;
  top: 50%;
}

button:disabled {
  background-color: #a1a2a3;
  color: rgb(39, 39, 39);
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.title {
  align-self: center;
  font-size: 35px;
}

.meals {
  align-items: center;
  background: #0fa36b;
  color: #464646;
  display: flex;
  flex-direction: column;
  font-family: sans-serif;
  height: 100vh;
  justify-content: center;
  width: 100%;
}

.rocksGlass {
  animation: shake 1s infinite ease;
  max-height: 80vh;
}

.buttons {
  font-size: 12px;
  height: 30px;
  padding: 5px;
  width: 120px;
}

.meals > .logo {
  animation: shake 1.5s infinite ease;
  color: aliceblue;
  font-family: Montserrat , sans-serif;
  font-size: 3em;
  font-weight: 900;
}

@keyframes shake {

  0% {
    transform: rotate(5deg);
  }

  50% {
    transform: rotate(-5deg);
  }

  100% {
    transform: rotate(5deg);
  }
}

@keyframes clink {

  0% {
    transform: rotate(-10deg);
  }

  50% {
    transform: rotate(20deg);
  }

  100% {
    transform: rotate(-10deg);
  }
}

